import { Component } from '@angular/core';
import { ActionSheetController  ,AlertController, IonicPage,NavController, NavParams, InfiniteScroll } from 'ionic-angular';
import { Auth ,NotificationProvider } from '../../../providers';
import { TranslateService } from '@ngx-translate/core';
import { Storage } from '@ionic/storage';


@IonicPage()
@Component({
  selector: 'page-cursosview-unidades',
  templateUrl: 'cursosview-unidades.html',
})
export class CursosviewUnidadesPage {

  public loading: boolean = true;
  items: any=[];
  items2: any=[];
  permisom: any=[];
    generated = '';
    public hasFilter: boolean = false;
   
  public gLimit: number = 50;
  public PageSegmet: string = 'foods';
  public gSearchQuery: string = '';
  username: string = "";
//private barcodeScanner:BarcodeScanner,
  constructor(public actionSheetCtrl: ActionSheetController,public alertCtrl: AlertController,public storage: Storage, public msj: NotificationProvider, public translateService: TranslateService
    ,public navCtrl: NavController,  public navParams: NavParams,   public regService: Auth
    //, private qrScanner: QRScanner
   
    ) {

    
  }

  
  public clearSearchg(): void {
    this.gSearchQuery = '';
  }

 
    



  pideregistros() { 
      let self = this;
      self.loading = true;
      this.regService.getReg(this.navParams.data.idplantilla +'/'+this.navParams.data.idempresa + '/todos','plantilladms').then((data) => {
        this.items=data;
        this.items2=data;
        self.loading = false;
  }, (err) => {
    this.msj.showError3('Error',err.error);
   // this.translateService.get('NO_OPERACION').subscribe((value) => { this.msj.showError3('Error',value);  })  
  });
  }
  ngOnInit() {
    this.storage.get('username').then((value) => {
      this.username = value;
  }); 
  }
  ionViewDidLoad() {
    this.pideregistros();
  }
  
  public loadMore(ev: InfiniteScroll) {
    this.gLimit += 50;
    setTimeout(() => ev.complete(), 1000);
  }



   participaRecord(item : any) : void
   {
   
      this.navCtrl.push('CursosviewModuloPage', { record : item,idempresa:this.navParams.data.idempresa,idplantilla:this.navParams.data.idplantilla,idunidad:item._id,nombrep:item.nombre });
   }
  

}



